body {
  margin: 0;
  padding: 0;
  font-family:  "Roboto Condensed", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 30px;
  overflow-y: scroll;
}

code {
  font-family:  "Roboto Condensed", sans-serif;
}

* {
  box-sizing: border-box;
}

.app {
  width: 100%;
  height: 100%;
	margin: 0;
  font-family: 'Roboto Condensed', sans-serif;
}
    .grid {
      width: 100%;
      height: 100%;
      display: grid;
      grid-template-columns: 288px 60px 1.7fr repeat(2, 1fr);
      grid-template-rows: repeat(16, 30px) auto;
      grid-template-areas: 
      "picture . title title title"
      "picture . title title title"
      "picture . subtitle subtitle subtitle"
      "picture . . . ."
      "picture . obj obj obj"
      "picture . obj obj obj"
      "picture . obj obj obj"
      "picture . studies courses skills"
      "picture . studies courses skills"
      "about . studies courses skills"
      "about . studies courses skills"
      "about . studies courses skills"
      "about . studies courses skills"
      "about . studies courses skills"
      "languages . studies courses skills"
      "languages . studies courses skills"
      "languages . work work work"
      "interests . work work work"
      ". . work work work"
    }
   
    /* #github {
      grid-area: github;
    }
    #linkedin {
      grid-area: linkedin;
    } */
    
    
      
    
   
   
      
   
   

        @media only screen and (max-width: 420px) {
          .app {
            padding: 10px;
            overflow-x: hidden;
          }
            .grid {
              grid-template-columns: 1fr 1fr;
              grid-template-rows: auto;
              grid-template-areas: 
              "picture picture"
              "picture picture"
              "picture picture"
              "title title"
              "subtitle subtitle"
              "about about"
              "about about"
              "obj obj"
              "obj obj"
              "studies studies"
              "studies studies"
              "studies studies"
              "courses courses"
              "courses courses"
              "work work"
              "work work"
              "work work"
              "work work"
              "work work"
              "skills skills"
              "skills skills"
              "languages languages"
              "languages languages"
              "interests interests"
            }
           
            .profilePic {
              max-width: 100%;
              height: 30%;
              /* max-height: 140px; */
              background-size: cover;
              /* -webkit-transform: scale(0.7);
              transform: scale(.7);  */
             -webkit-transform-origin: center;
              transform-origin: center;
              /* align-items: center; */ 
            }
            h1 {
              font-size: 36px;
            }
            .subtitle {
              padding: 0 0 10px 0;
            }
            .line-subtitle {
              display: none;
            }
            .about {
              padding-left: 5px;
            }
            .about-title {
              display: none;
            }
            .line-about {
              display: none;
            }
            .languages,
            .interests {
              padding-left: 0;
            }
            .languages {
              margin-top: 20px;
            }
            .topic-title {
              font-size: 14px;
              margin-top: 18px;
            }
            p {
              text-align: justify;
              line-height: 1.4;
            }
            .line-objectives {
              width: 143px;
              margin: 0 0 8px 0;
            }
            .line-studies {
              width: 68px;
              margin: 0 0 8px 0;
            }
            .line-courses {
              margin: 0 0 8px 0; 
            }
            .line-skills {
              margin: 0 0 8px 0;
            }
            .my-skills {
              margin-bottom: 16px;
            }
            .line-languages {
              margin: 0 0 8px 0;
            }
            .line-interests {
              margin: 0 0 8px 0;
            }
            .interestsIcons {
              margin: 10px 0 20px 0;
            }
            .studies_content {
              display: grid;
              grid-template-columns: 85px 400px;  
            }
            .work_content {
              display: grid;
              grid-template-columns: 1fr;
              text-align: justify; 
            }
            .date_1 {
              max-width: 50px;
            }
            .work_experience1,
            .work_experience2 {
              display: grid;
              grid-template-columns: 80px 270px;
            }
            .date,
            .date_1,
            .date_2 {
              font-size: 12px;
            }
        }

.courses {
  grid-area: courses;
}
  .courses--title {
    font-size: 14px;
    margin-top: 18px;
    font-weight: 700;
  }
  .courses--line {
    width: 54px;
    border-bottom: 2px solid rgb(22, 7, 163);
    position: relative;
    min-height: 0;
    margin: 0 0 16px 0;
  }
  .courses--topic {
    font-size: 14px;
    font-weight: 700;
    line-height: 1.7;
  }
  .courses--school {
    margin-bottom: 10px;
    font-size: 14px;
  }

  @media only screen and (max-width: 420px) {
    .courses {
      padding: 10px 0 0 0;;
      overflow-x: hidden;
    }
    .courses--title {
      font-size: 14px;
      margin-top: 18px;
    }
    .courses--line {
      width: 54px;
      margin: 0 0 10px 0;
    }
  }
.work {
  grid-area: work;
  display: grid;
  grid-template-rows: 40px 10px auto;
}
  .work--title {
    font-size: 14px;
    margin-top: 24px;
    font-weight: 700;
  }
  .work--line {
    width: 110px;
    border-bottom: 2px solid rgb(22, 7, 163);
    position: relative;
    min-height: 0;
    margin: 0 0 16px 0;
  }
  .work--content {
    display: grid;
    grid-template-columns: repeat(2, auto);
    margin-top: 10px;
    text-align: justify;
  }
    .work--date{
      font-weight: 700;
      font-size: 14px;
    } 
    .job--title {
      font-weight: 700;
      font-size: 14px;
    }
    .job--description {
      font-size: 14px;
      margin: 5px 0 15px 0;
    }
    
    .work--experience-1,
    .work--experience-2 {
      display: grid;
      grid-template-columns: 1fr;
      margin: 0;
    }
    .work--experience-1 {
      margin-right: 30px;
    }
    .work--experience-item {
      display: grid;
      grid-template-columns: 80px auto;
      margin: 0;
    }
    .job--description {
      grid-column: 2/2;
    }

    @media only screen and (max-width: 420px) {
      .work {
        padding: 10px 0 0 0;
        overflow-x: hidden;
      }
      .work--content {
        display: grid;
        grid-template-columns: 1fr;
        text-align: justify; 
      }
      .work--date {
        font-size: 13px;
      }
       
      .work--experience-1,
      .work--experience-2 {
        display: grid;
        grid-template-columns: 1fr;
        margin: 0;
      }
      .work--experience-item {
        display: grid;
        grid-template-columns: 80px auto;
        margin: 0;
      }
      .job--description {
        grid-column: 2/2;
      }
    }
.studies {
  grid-area: studies;
}
  .studies--title{
    font-size: 14px;
    margin-top: 18px;
    font-weight: 700;
  }
  .studies--line {
    width: 50px;
    border-bottom: 2px solid rgb(22, 7, 163);
    position: relative;
    min-height: 0;
    margin: 0 0 16px 0;
  }
  .studies--content{
    display: grid;
    grid-template-columns: 0.6fr auto;
    grid-template-rows: repeat(4, auto)
  }
  .studies--item {
    grid-column: 10px 10px;
  }
    .studies--date {
      display: grid;
      grid-template-rows: repeat(4, auto);
      font-weight: 700;
      line-height: 1.7;
      font-size: 14px;
    }
      .studies--topic {
        font-size: 14px;
        font-weight: 700;
        line-height: 1.7;
      }
      
      .studies--school {
        margin-bottom: 10px;
        font-size: 14px;
      }

  @media only screen and (max-width: 420px) {
    .studies{
      padding: 10px 0 0 0;
      overflow-x: hidden;
    }
    .studies--title {
      font-size: 14px;
      margin-top: 18px;
    }
    .studies--line {
      width: 48px;
      margin: 0 0 8px 0;
    }
    .studies--content {
      display: grid;
      grid-template-columns: 85px auto;
      line-height: 1;
    }
    .studies--date {
      font-size: 13px;
      grid-template-rows: repeat(4, 15px)
    }
  }
.skills {
  grid-area: skills;
}
  .skills--title {
    font-size: 14px;
    margin-top: 18px;
    font-weight: 700;
  }
  .skills--line {
    width: 40px;
    border-bottom: 2px solid rgb(22, 7, 163);
    position: relative;
    min-height: 0;
    margin: 0 0 16px 0;
  }
  .skills--specifics {
    font-weight: normal;
  }
  .skills-topic {
    font-size: 14px;
    font-weight: 700;
    line-height: 1.7;
  }

  @media only screen and (max-width: 420px) {
    .skills{
      padding: 10px 0 0 0;
      overflow-x: hidden;
    }
    .skills--title {
      font-size: 14px;
      margin-top: 18px;
    }
    .skills--line {
      margin: 0 0 8px 0;
    }

    .skills--myskills {
      margin-bottom: 10px;
    }
  }

  
.objectives {
  grid-area: obj;
}
  .objectives--line {
    width: 120px;
    border-bottom: 2px solid rgb(22, 7, 163);
    position: relative;
    min-height: 0;
    margin: 0 0 16px 0;
  }
  .objectives--title{
    font-weight: 700;
    font-size: 14px;
  }
  p {
    align-self: center;
    margin: 0;
    line-height: 1.7;
    font-size: 15px;
  }

  @media only screen and (max-width: 420px) {
    .objectives--title {
      font-size: 14px;
      margin-top: 18px;
    }
    p {
      text-align: justify;
      line-height: 1.2;
    }
    .objectives--line {
      width: 120px;
      margin: 0 0 8px 0;
    }
  }
.title {
  grid-area: title;
  position: sticky;
  position: -webkit-sticky;
  top: -1px;
  background-color: #fff;
  z-index: 9999;
}
  h1 {
    margin: 0;
    font-family: 'Parisienne';
    font-size: 50px;
  }

  @media only screen and (max-width: 420px) {
    h1 {
      font-size: 36px;
    }
  }
.subtitle {
  display: flex;
  grid-area: subtitle;
  padding: 8px 0 0 0;
  align-items: flex-start
  /* position: sticky;
  top: -10px;
  z-index: 9997; */
}
  .position{
    font-family: 'sans-serif';
    font-size: 18px;
    font-weight: normal;
    position: -webkit-sticky;
    position: sticky;
    top: -1px;
    background-color: #fff;
    z-index: 9999;
    
  }
  .subtitle--line {
    background-color: rgb(22, 7, 163);
    position: relative;
    min-height: 0;
    margin: 8px 0 26px 8px;
  }

  @media only screen and (max-width: 420px) {
    .subtitle {
      padding: 0 0 10px 0;
    }
    .subtitle--line {
      display: none;
    }
  }
.picture {
  grid-area: picture;
}
  .picture--profile {
    height: 260px;
    max-width: 100%;
  }

  @media only screen and (max-width: 420px) {
    .picture--profile {
      max-width: 100%;
      height: 30vh;
      background-size: cover;
      -webkit-transform-origin: center;
      transform-origin: center;
    }
  }
.about {
  grid-area: about;
  border-top: 20px solid  rgb(22, 7, 163);
  padding: 10px 10px;
}
  .about--title {
    font-weight: 700;
    font-size: 14px;
  }
  .about--line{
    width: 40px;
    border-bottom: 2px solid rgb(22, 7, 163);
    position: relative;
    min-height: 0;
    margin-bottom: 1em;
  }
  .about--info-local{
    display: grid;
    grid-template-columns: 40px auto;
    max-width: 240px;
    font-size: 14px;
  }
  .about--info-email {
    display: grid;
    grid-template-columns: 40px auto;
    max-width: 240px;
    font-size: 14px;
  } 
  .about--lnr {
    height: 25px;
    width: 25px;
  }
  .about--lnr-github {
    margin: 5px 5px 5px 0;
  }
  p {
    align-self: center;
    margin: 0;
    line-height: 1.7;
  }
  @media only screen and (max-width: 420px) {
    .about {
      padding-left: 5px;
    }
    .about--title,
    .about--line {
      display: none;
    }
  }
    .languages {
      grid-area: languages;
      padding: 0 10px;
    }
      .languages--title {
        font-weight: 700;
        font-size: 14px;
      }
      .languages--line {
        width: 71px;
        border-bottom: 2px solid rgb(22, 7, 163);
        position: relative;
        min-height: 0;
        margin-bottom: 1em;
      }
      ul {
        list-style: none;
        padding: 0;
        margin-top: 5px;
      }
        li {
          text-align: left; 
          padding: 4px 0 4px 0;
          margin-bottom: 3px;
          font-size: 14px;
        }

    @media only screen and (max-width: 420px) {
      .languages{
        padding-left: 0;
        margin-top: 10px;
      }
      .languages--line {
        margin: 0 0 8px 0;
      }
      ul {
        margin: 0
      }
    }
.interests {
  grid-area: interests;
  padding: 0 10px;
}
  .interests--title {
    font-weight: 700;
    font-size: 14px;
  }
  .interests--line {
    width: 64px;
    border-bottom: 2px solid rgb(22, 7, 163);
    position: relative;
    min-height: 0;
    margin-bottom: 1em;
  }
  .interests--icons {
    margin-top: 20px;
  }
    .interests--row-icons {
      display: grid;
      grid-template-columns: 40px 40px 40px 40px 40px 40px;
    }
    .interests--lnr {
      height: 25px;
      width: 25px;
    }
    @media only screen and (max-width: 420px) {
      .interests{
        padding-left: 0;
        margin-top: 15px;
      }
      .interests--line {
        margin: 0 0 8px 0;
      }
      .interests--icons {
        margin: 10px 0 20px 0;
      }
    }
